@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserrat-semi-bold.woff2") format("woff2"), url("../fonts/montserrat-semi-bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-size: 16px;
  line-height: 1.5;
  font-weight: 400;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  overflow: hidden;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 60px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.button {
  display: block;
  padding: 10px;
  font-size: 14px;
  line-height: 1.5;
  font-weight: 600;
  text-align: center;
  color: #011c40;
  background-color: #f9fbfd;
  border: 0;
  transition: opacity, color 0.3s ease;
  user-select: none;
  cursor: pointer;
}
.button:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.button--disabled {
  opacity: 0.4;
  pointer-events: none;
}
@media (hover: hover) {
  .button:hover:not(.focus-visible) {
    color: #6ba9d7;
    opacity: 0.8;
  }
}
.button.focus-visible:focus {
  color: #6ba9d7;
  opacity: 0.8;
}
.button.focus-visible:active {
  color: #011c40;
  opacity: 0.6;
}

.header {
  min-width: 320px;
  min-height: 650px;
  background-color: #6ba9d7;
  background-image: url("../img/background/intro-bg-desktop.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border: 1px solid #6ba9d7;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.js-webp-exception .header {
  background-image: url("../img/background/intro-bg-desktop.jpg");
}
@media (max-width: 1023px) {
  .header {
    min-height: 638px;
    background-image: url("../img/background/intro-bg-tablet.webp");
    border: 0;
    box-shadow: none;
  }
  .js-webp-exception .header {
    background-image: url("../img/background/intro-bg-tablet.jpg");
  }
}
@media (max-width: 767px) {
  .header {
    min-height: 500px;
    background-image: url("../img/background/intro-bg-mobile.webp");
  }
  .js-webp-exception .header {
    background-image: url("../img/background/intro-bg-mobile.jpg");
  }
}
@media (min-resolution: 144dpi) and (max-width: 767px), (min-resolution: 1.5dppx) and (max-width: 767px) {
  .header {
    background-image: url("../img/background/intro-bg-mobile@2x.webp");
  }
  .js-webp-exception .header {
    background-image: url("../img/background/intro-bg-mobile@2x.jpg");
  }
}
@media (min-resolution: 144dpi) and (max-width: 1023px), (min-resolution: 1.5dppx) and (max-width: 1023px) {
  .header {
    background-image: url("../img/background/intro-bg-tablet@2x.webp");
  }
  .js-webp-exception .header {
    background-image: url("../img/background/intro-bg-tablet@2x.jpg");
  }
}
@media (min-resolution: 144dpi) and (min-width: 1024px), (min-resolution: 1.5dppx) and (min-width: 1024px) {
  .header {
    background-image: url("../img/background/intro-bg-desktop@2x.webp");
  }
  .js-webp-exception .header {
    background-image: url("../img/background/intro-bg-desktop@2x.jpg");
  }
}

.header__section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 161px;
  padding: 30px 0;
}
@media (max-width: 1023px) {
  .header__section {
    margin-bottom: 187px;
    padding: 20px 0;
  }
}
@media (max-width: 767px) {
  .header__section {
    min-height: 69px;
    margin-bottom: 107px;
  }
}

.header__logo {
  flex: 0 0 auto;
  margin-top: 4px;
  margin-right: 15px;
  color: #f9fbfd;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .header__logo:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.header__logo.focus-visible:focus {
  opacity: 0.8;
}
.header__logo.focus-visible:active {
  opacity: 0.6;
}
@media (max-width: 1023px) {
  .header__logo {
    margin-top: 6px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    margin: 0;
  }
}

.header__logo svg {
  width: 70px;
  height: 38px;
}
@media (max-width: 1023px) {
  .header__logo svg {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .header__logo svg {
    width: 42px;
    height: 22px;
  }
}

@media (max-width: 767px) {
  .js-exception .header__section {
    margin-bottom: 0;
  }
}

@media (max-width: 767px) {
  .mobile-menu {
    position: relative;
  }
}

@media (max-width: 767px) {
  .is-open .mobile-menu__logo {
    z-index: 2;
    position: absolute;
    top: 20px;
    left: 50px;
    color: #011c40;
  }
}

.mobile-menu__toggle {
  display: none;
}
@media (max-width: 767px) {
  .mobile-menu__toggle {
    position: relative;
    display: block;
    width: 24px;
    height: 24px;
    margin-top: -8px;
    padding: 0;
    background-color: rgba(255, 255, 255, 0);
    border: 0;
    cursor: pointer;
  }
  .mobile-menu__toggle::after {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    content: "";
    width: 24px;
    height: 2px;
    margin: auto;
    background-color: #f9fbfd;
    box-shadow: 0 6px 0 0 #f9fbfd, 0 -6px 0 0 #f9fbfd;
  }
}

@media (max-width: 767px) {
  .is-open .mobile-menu__toggle {
    z-index: 2;
    position: absolute;
    top: 28px;
    right: 10px;
  }
}

@media (max-width: 767px) {
  .is-open .mobile-menu__toggle::before,
.is-open .mobile-menu__toggle::after {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    content: "";
    width: 18px;
    height: 2px;
    margin: auto;
    background-color: #011c40;
    box-shadow: none;
  }
}

@media (max-width: 767px) {
  .is-open .mobile-menu__toggle::before {
    transform: rotate(45deg);
  }
}

@media (max-width: 767px) {
  .is-open .mobile-menu__toggle::after {
    transform: rotate(-45deg);
  }
}

@media (max-width: 767px) {
  .is-close .mobile-menu__navigation {
    display: none;
  }
}

@media (max-width: 767px) {
  .is-open .mobile-menu__navigation {
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 43, 102, 0.5);
    overflow-y: scroll;
  }
}

@media (max-width: 767px) {
  .is-open .mobile-menu__list {
    min-height: 100vh;
    margin-left: 35px;
    background-color: #f9fbfd;
  }
}

@media (max-width: 767px) {
  .js-exception .mobile-menu__toggle {
    display: none;
  }
}

@media (max-width: 767px) {
  .js-exception .mobile-menu__logo {
    position: static;
    color: #f9fbfd;
  }
}

@media (max-width: 767px) {
  .js-exception .mobile-menu__navigation {
    position: static;
    display: block;
    background-color: #d9e6f4;
  }
}

.navigation__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .navigation__list {
    flex-direction: column;
    padding: 74px 25px 25px;
  }
}

.navigation__item {
  display: flex;
}

.navigation__item:not(:last-child) {
  margin-right: 34px;
}
@media (max-width: 1023px) {
  .navigation__item:not(:last-child) {
    margin-right: 24px;
  }
}
@media (max-width: 767px) {
  .navigation__item:not(:last-child) {
    margin: 0;
    margin-bottom: 4px;
  }
}

.navigation__link {
  display: block;
  padding: 12px 0;
  font-size: 16px;
  line-height: 1.25;
  font-weight: 500;
  color: #f9fbfd;
  transition: color 0.3s ease;
}
@media (hover: hover) {
  .navigation__link:hover:not(.focus-visible) {
    color: #6ba9d7;
  }
}
.navigation__link.focus-visible:focus {
  color: #6ba9d7;
}
.navigation__link.focus-visible:active {
  color: #d8d8d9;
}
@media (max-width: 1023px) {
  .navigation__link {
    font-size: 14px;
    line-height: 1.21;
  }
}
@media (max-width: 767px) {
  .navigation__link {
    font-size: 18px;
    line-height: 1.22;
    color: #011c40;
  }
}

@media (max-width: 1023px) {
  .navigation--footer .navigation__link {
    font-size: 12px;
    line-height: 1.25;
  }
}
@media (max-width: 767px) {
  .navigation--footer .navigation__link {
    padding: 6px 0;
    color: #f9fbfd;
  }
}

@media (max-width: 767px) {
  .navigation--footer .navigation__list {
    padding: 0;
  }
}

@media (max-width: 767px) {
  .navigation--footer .navigation__item:not(:last-child) {
    margin-bottom: 5px;
  }
}

@media (max-width: 767px) {
  .js-exception .navigation__list {
    padding: 15px;
  }
}

