  max-width: 130px;
  margin: 0;
  margin-right: -5px;
  padding: 0;
  list-style: none;
}
@media (max-width: 1023px) {
  .social {
    grid-template-columns: repeat(auto-fit, 26px);
    column-gap: 12px;
  }
}
@media (max-width: 767px) {
  .social {
    margin-right: 0;
    margin-left: -9px;
  }
}

.social__link {
  display: flex;
  width: 30px;
  height: 30px;
  padding: 5px;
  color: #f9fbfd;
  transition: color 0.3s ease;
}
@media (hover: hover) {
  .social__link:hover:not(.focus-visible) {
    color: #6ba9d7;
  }
}
.social__link.focus-visible:focus {
  color: #6ba9d7;
}
.social__link.focus-visible:active {
  color: #d8d8d9;
}
@media (max-width: 1023px) {
  .social__link {
    width: 26px;
    height: 26px;
  }
}

.social__icon {
  height: 20px;
  margin: auto;
}
@media (max-width: 1023px) {
  .social__icon {
    height: 16px;
  }
}